import React, { Component } from 'react';
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

class Portfolio extends Component {
    componentDidMount() {
        window.scrollTo(0, 0);
    }
    render() {
        if (this.props.data) {
            var projects = this.props.data.map(function (projects) {
                var projectImage = 'images/' + projects.image;
                return (
                    <div key={projects.title} className="columns2 portfolio-item">
                        <div className="item-wrap">
                            <img alt={projects.title} src={projectImage} />
                                <div className="overlay">
                                    <div className="portfolio-item-meta2">
                                        <h5>{projects.title}</h5>
                                    </div>
                            </div>
                        </div>
                        <p className="portfolio-description">{projects.description}</p>
                    </div>
                )
            })
        }

        return (
            <div>
            <nav id="back-wrap">
                    <ul id="back" className="nav">
                        <li><Link className="smoothscroll" to="/personal">Back</Link></li>
                </ul>
            </nav>
            <section id="portfolio">
                <h1>{this.props.name}</h1>
                <h3>{this.props.info}</h3>
                <div className="row">

                    <div>

                        <div id="portfolio-wrapper" >
                            {projects}
                        </div>
                    </div>
                </div>
                </section>
            </div>
        );
    }
}

export default Portfolio;